.wrapped_input{display:block;font-size:16px;font-family:sans-serif;font-weight:700;color:#007cb2;line-height:1.3;padding:.6em 1.4em .5em .8em;width:100%;max-width:100%;box-sizing:border-box;margin:5px;border:1px solid #aaa;box-shadow:0 1px 0 1px rgba(0,0,0,0.04);border-radius:.5em;-moz-appearance:none;-webkit-appearance:none;appearance:none;background-color:#fff;background-repeat:no-repeat, repeat;background-position:right .7em top 50%, 0 0;background-size:.65em auto, 100%}.wrapped_input:hover{border-color:#888}.wrapped_input:focus{border-color:#aaa;box-shadow:0 0 1px 3px #9edeeb;box-shadow:0 0 0 3px -moz-mac-focusring;color:#005783;outline:none}.wrapped_input__wrapper{margin:20px;width:200px;display:flex;flex-direction:column}.wrapped_input__description{color:#005783;font-weight:700;align-self:center;padding:2px;border-bottom:solid 2px #007cb2}
