.article{width:25%;min-width:450px;display:flex;flex-direction:column;margin:2%;border-top:solid 6px #b7e2e2;border-bottom:solid 6px #b7e2e2;overflow:hidden}.article__image{margin:10px;width:90%}.article__content{padding:10px}.article__title{padding:5px;border-bottom:solid 3px #8bb6b6}.article__source{padding:5px 15px;margin-top:5px;font-size:large;background-color:#b7e2e2;border-radius:40px}.article__upper_row,.article__lower_row,.article__footer{margin:10px;display:flex;flex-direction:column}.article__upper_row{flex:1;align-items:center;justify-content:center}.article__lower_row{flex:10;align-items:center;justify-content:center}.article__description{padding:0 40px 3px;align-self:center;margin:0 0 10px 0;border-bottom:solid 3px #b7e2e2}.article__footer{flex:1;margin:10px;display:flex;align-items:flex-end}.article__footer__author,.article__footer__published_at,.article__footer__url{padding:5px 15px;margin-top:3px}.article__footer__author,.article__footer__published_at{font-size:17px;background-color:#cddfd1;border-radius:40px}.article__footer__url{background-color:#a2cfcf;font-size:16px;border-radius:20px;text-decoration:none;color:darkslateblue}
